*, :before, :after {
  box-sizing: border-box;
}

html {
  tab-size: 4;
  -webkit-text-size-adjust: 100%;
  line-height: 1.15;
}

body {
  margin: 0;
  font-family: system-ui, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, AppleSystem, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans, Ubuntu, Cantarell, Helvetica Neue, -apple-system, Segoe UI, Roboto, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
}

hr {
  height: 0;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b, strong {
  font-weight: bolder;
}

code, kbd, samp, pre {
  font-family: ui-monospace, SFMono-Regular, Consolas, Liberation Mono, Menlo, monospace;
  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

:root {
  --primary-color: #84a178;
  --second-color: #fcfcfc;
  --additional-color: #131313;
  --accent-link-color: #f8d24e;
  --focus-color: #0a8817;
  --disabled-color: #ababab;
  --main-font: "Montserrat", sans-serif;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  opacity: 0;
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

body {
  font-family: var(--main-font);
  color: var(--second-color);
  background-color: var(--primary-color);
}

p, h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.link {
  text-decoration: none;
}

.list {
  list-style: none;
}

.btn {
  text-align: center;
  color: #000;
  background: var(--accent-link-color);
  cursor: pointer;
  border: 0;
  border-radius: 10px;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.21;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    width: 1200px;
    padding: 0 16px;
  }
}

.backdrop {
  width: 100%;
  height: 100%;
  opacity: 1;
  z-index: 10;
  background: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow-y: scroll;
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.is-hidden .product-modal {
  transform: translate(-50%, -10%)scale(1.3);
}

.product-modal {
  max-width: 280px;
  width: 100%;
  height: 991px;
  max-height: 100%;
  background-color: var(--second-color);
  border: 2px solid var(--accent-link-color);
  border-radius: 10px;
  padding: 28px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  overflow: auto;
  transform: translate(-50%, -50%)scale(1);
}

@media screen and (min-width: 768px) {
  .product-modal {
    max-width: 568px;
    height: 644px;
    padding: 32px;
  }
}

.product-modal__title {
  color: var(--additional-color);
  justify-content: center;
  align-items: center;
  margin-bottom: 83px;
  font-family: Seymour One, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
  display: flex;
}

@media screen and (min-width: 768px) {
  .product-modal__title {
    margin-bottom: 103px;
  }
}

.btn-close {
  width: 30px;
  height: 25px;
  background-color: var(--second-color);
  cursor: pointer;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition: color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 15px;
  right: 24px;
}

.btn-close:hover, .btn-close:focus {
  color: var(--accent-link-color);
}

.btn-close__icon {
  fill: currentColor;
}

.product-modal__form {
  z-index: 10;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.product-modal__list {
  gap: 82px;
  margin-bottom: 40px;
  display: flex;
}

@media screen and (max-width: 767px) {
  .product-modal__list {
    flex-direction: column;
  }
}

@media screen and (min-width: 768px) {
  .product-modal__list {
    gap: 32px;
    margin-bottom: 40px;
  }
}

.product-modal__item {
  width: 132px;
  height: 135px;
  border-radius: 15px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.product-modal__item--green {
  border: 2px solid #96af8c;
}

.product-modal__item--yellow {
  border: 2px solid #efd478;
}

.product-modal__item--brown {
  border: 2px solid #d6936d;
}

.product-modal__gradient {
  width: 132px;
  height: 132px;
  border-radius: 50%;
  position: absolute;
  bottom: 45%;
}

.product-modal__gradient--green {
  background-image: linear-gradient(0deg, rgba(156, 190, 143, .7), rgba(156, 190, 143, .7));
  box-shadow: 0 5.26027px 19.726px rgba(10, 136, 23, .25);
}

.product-modal__gradient--yellow {
  background-image: linear-gradient(0deg, rgba(248, 210, 78, .7), rgba(248, 210, 78, .7));
  box-shadow: 0 5.26027px 19.726px rgba(153, 114, 23, .25);
}

.product-modal__gradient--brown {
  background-image: linear-gradient(0deg, rgba(213, 117, 63, .7), rgba(213, 117, 63, .7));
  box-shadow: 0 5.26027px 19.726px rgba(0, 0, 0, .25);
}

.product-modal__picture {
  width: 132px;
  height: 132px;
  border-radius: 50%;
  position: absolute;
  bottom: 5%;
}

.product-modal__form-checkbox {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  justify-content: center;
  align-items: flex-end;
  padding-bottom: 15px;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.product-modal__checkbox {
  position: absolute;
}

.product-modal__checkbox:checked + .product-modal__form-checkbox--green {
  background-color: #aec6a5;
}

.product-modal__checkbox:checked + .product-modal__form-checkbox--yellow {
  background-color: #efd478;
}

.product-modal__checkbox:checked + .product-modal__form-checkbox--brown {
  background-color: #d6936d;
}

.product-modal__text {
  text-align: center;
  letter-spacing: .04em;
  text-transform: uppercase;
  color: var(--additional-color);
  font-size: 16px;
  font-weight: 700;
  line-height: 1.25;
}

.product-modal__form-field {
  margin-bottom: 25px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .product-modal__form-field {
    margin-bottom: 16px;
  }
}

.product-modal__form-comment {
  margin-bottom: 28px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .product-modal__form-comment {
    margin-bottom: 32px;
  }
}

.product-modal__label {
  color: var(--additional-color);
  font-size: 12px;
  line-height: 1.4;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 12px;
  transform: translateY(-50%);
}

@media screen and (min-width: 768px) {
  .product-modal__label {
    font-size: 14px;
  }
}

.product-modal__label-comment {
  color: var(--additional-color);
  font-size: 12px;
  line-height: 1.4;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 12%;
  left: 12px;
}

@media screen and (min-width: 768px) {
  .product-modal__label-comment {
    font-size: 14px;
  }
}

.product-modal__input {
  width: 224px;
  height: 32px;
  color: var(--additional-color);
  border: 1px solid var(--primary-color);
  background-color: rgba(248, 210, 78, .1);
  border-radius: 10px;
  outline: none;
  padding: 8px 12px;
  font-size: 12px;
  line-height: 1.4;
  display: block;
}

.product-modal__input:invalid:not(:focus):not(:placeholder-shown) {
  border: 1px solid red;
  outline: none;
}

@media screen and (min-width: 768px) {
  .product-modal__input {
    width: 368px;
    height: 42px;
  }
}

.product-modal__comment {
  width: 224px;
  height: 65px;
  color: var(--additional-color);
  border: 1px solid var(--primary-color);
  resize: none;
  background-color: rgba(248, 210, 78, .1);
  border-radius: 10px;
  outline: none;
  padding: 8px 12px;
  font-size: 12px;
  line-height: 1.4;
  display: block;
}

@media screen and (min-width: 768px) {
  .product-modal__comment {
    width: 368px;
    height: 84px;
    font-size: 14px;
  }
}

.product-modal__input:focus + .product-modal__label, .product-modal__input:not(:placeholder-shown) + .product-modal__label {
  font-weight: 700;
  transform: translateY(-38px);
}

@media screen and (min-width: 768px) {
  .product-modal__input:focus + .product-modal__label, .product-modal__input:not(:placeholder-shown) + .product-modal__label {
    font-weight: 700;
    transform: translate(-85px, -10px);
  }
}

.product-modal__comment:focus + .product-modal__label-comment, .product-modal__comment:not(:placeholder-shown) + .product-modal__label-comment {
  font-weight: 700;
  transform: translateY(-30px);
}

@media screen and (min-width: 768px) {
  .product-modal__comment:focus + .product-modal__label-comment, .product-modal__comment:not(:placeholder-shown) + .product-modal__label-comment {
    font-weight: 700;
    transform: translate(-105px, -1px);
  }
}

.product-modal__btn {
  width: 114px;
  height: 42px;
  transition: color .25s cubic-bezier(.4, 0, .2, 1), background-color .25s cubic-bezier(.4, 0, .2, 1);
  display: block;
}

.product-modal__btn:hover {
  color: #000;
  background-color: var(--focus-color);
}

.product-modal__btn:focus {
