.footer__tablet {
  border-bottom: 1px solid #e1e1e1;

  @include tablet {
    display: flex;
    justify-content: center;
  }
}

.footer__title {
  margin-bottom: 16px;
  width: 232px;
  font-size: 18px;
  text-transform: uppercase;
  line-height: calc(22 / 18);

  @include tablet {
    width: 352px;
    margin-bottom: 8px;
    font-size: 26px;
    line-height: calc(32 / 26);
    letter-spacing: 0.04em;
  }

  @include desktop {
    width: 504px;
    margin-bottom: 14px;
    font-weight: 400;
    font-size: 30px;
    line-height: calc(37 / 30);
    letter-spacing: 0.04em;
  }
}

.footer__text {
  margin-bottom: 60px;
  width: 200px;
  font-weight: 700;
  font-size: 34px;
  line-height: calc(41 / 34);
  letter-spacing: 0.04em;
  text-transform: uppercase;

  @include tablet {
    width: 352px;
    margin-bottom: 32px;
    font-size: 48px;
    line-height: calc(59 / 48);
    letter-spacing: 0.06em;
  }

  @include desktop {
    min-width: 504px;
    margin-bottom: 60px;
    font-size: 58px;
    line-height: calc(71 / 58);
    letter-spacing: 0.06em;
  }
}

.container .footer__social {
  width: 100%;
  padding-right: 0px;
}

.footer__subtitle {
  list-style: none;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-bottom: 20px;

  @include tablet {
    padding-top: 60px;
    margin-bottom: 34px;
  }

  @include desktop {
    margin-bottom: 40px;
    padding-top: 18px;
  }
}

.footer__item {
  margin-right: 20px;

  &:nth-child(3) {
    margin-right: 0px;
  }
}

.footer__link {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  color: #84a178;
  background-color: var(--accent-link-color);
  fill: currentColor;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  &:hover,
  &:focus {
    color: var(--second-color);
  }
}

.footer__info {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 0;
  margin-bottom: 40px;
  font-weight: 700;
  font-size: 12px;
  line-height: calc(22.32 / 12);
  text-align: right;

  color: var(--additional-color);

  @include tablet {
    margin-bottom: 60px;
    font-size: 14px;
    line-height: calc(26.04 / 12);
  }

  @include desktop {
    font-size: 16px;
    line-height: calc(29.76 / 16);
    letter-spacing: 0.02em;
  }
}

.footer__contact {
  padding-left: 5px;
  font-weight: 700;
  font-size: 12px;
  line-height: calc(22.32 / 12);
  text-decoration: none;
  letter-spacing: 0.02em;

  color: var(--second-color);

  @include tablet {
    font-size: 14px;
    line-height: calc(26.04 / 12);
  }
  @include desktop {
    font-size: 16px;
    line-height: calc(29.76 / 16);
  }
}

.footer__copyright {
  display: block;
  margin: 0px auto;
  padding-top: 20px;
  padding-bottom: 20px;

  max-width: 160px;
  text-align: center;
  font-weight: 700;
  font-size: 14px;
  line-height: calc(21.28 / 14);
  letter-spacing: 0.02em;
  text-align: center;
  color: #7a7a7a;
  @include tel {
    padding-top: 20px;
    padding-bottom: 24px;
  }

  @include tablet {
    max-width: 298px;
    line-height: calc(26.04 / 14);
    padding-top: 12px;
    padding-bottom: 12px;
  }
  @include desktop {
    font-size: 12px;
    line-height: calc(22.32 / 12);
  }
}
